
import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence, Variants } from 'framer-motion';
import { Menu, X, ArrowUpRight, Sun, Moon, Twitter, Github, Linkedin, Instagram, Mail, Globe, Smartphone, Palette, Share2, TrendingUp, ChevronRight, ChevronLeft, Target, Rocket, Shield, Cpu, Zap, BarChart3, Fingerprint } from 'lucide-react';
import Link from 'next/link';

interface NavbarProps {
  isDark: boolean;
  toggleTheme: () => void;
}

const Navbar: React.FC<NavbarProps> = ({ isDark, toggleTheme }) => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [showMegaMenu, setShowMegaMenu] = useState(false);
  const [activeTab, setActiveTab] = useState<'development' | 'marketing' | 'ventures'>('development');
  const [hoveredItem, setHoveredItem] = useState<{ name: string; detail: string; longDesc: string; href: string } | null>(null);
  const [isMobileSubMenuOpen, setIsMobileSubMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 20);
    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
      setIsMobileSubMenuOpen(false);
    }
  }, [isOpen]);

  const links = [
    { name: 'Home', href: '/', desc: 'Preet Tech Home' },
    { name: 'About', href: '/about', desc: 'The Preet Edge' },
    { name: 'Services', href: '/#services', desc: 'Strategic Multipliers', hasMegaMenu: true },
    { name: 'Case Studies', href: '/#case-studies', desc: 'Measurable Outcomes' },
    { name: 'Blog', href: '/#insights', desc: 'Digital Logic' },
    { name: 'Contact', href: '/#contact', desc: 'Start a Journey' },
  ];

  const megaMenuContent = {
    development: {
      title: 'Digital Engineering',
      desc: 'High-performance code for scalable digital infrastructure.',
      items: [
        { name: 'Website Development', icon: Globe, href: '/services/website-development', detail: 'Eco-Budget & Advance Platforms', longDesc: 'We engineer blazingly fast websites. Choose between our Eco-Budget path for rapid deployment or Advance Websites for custom enterprise features.' },
        { name: 'App Development', icon: Smartphone, href: '/services/app-development', detail: 'Native & Cross-platform reach', longDesc: 'Building high-performance mobile applications with seamless user experiences across iOS and Android ecosystems.' },
        { name: 'Software Development', icon: Cpu, href: '/services/software-development', detail: 'Custom enterprise logic', longDesc: 'Architecting complex software systems tailored to your business operations, ensuring efficiency and long-term scalability.' },
        { name: 'Tools for Business', icon: Zap, href: '/services/business-tools', detail: 'Internal workflow automation', longDesc: 'Custom internal tools and integrations that automate repetitive tasks and optimize your business intelligence.' },
      ]
    },
    marketing: {
      title: 'Growth & Reach',
      desc: 'Data-driven strategies to amplify your brand authority.',
      items: [
        { name: 'Performance Marketing', icon: TrendingUp, href: '/services/performance-marketing', detail: 'Profit-focused campaigns', longDesc: 'Scientific approach to advertisement. We optimize for conversion and ROI across all major digital ad platforms.' },
        { name: 'Social Media Handling', icon: Share2, href: '/services/social-media-handling', detail: 'Community & Content management', longDesc: 'Engineering your social presence to build high-conversion communities and maintain consistent brand authority.' },
        { name: 'Partnership Marketing', icon: Target, href: '/services/partnership-marketing', detail: 'Strategic brand collaborations', longDesc: 'We connect you with key partners to amplify reach and build mutual authority through strategic digital alliances.' },
      ]
    },
    ventures: {
      title: 'Business Launch',
      desc: 'Everything you need to successfully enter the market.',
      items: [
        { name: 'Start Your Business', icon: Rocket, href: '/services/start-your-business', detail: 'End-to-end setup protocol', longDesc: 'From company registration to digital infrastructure. We handle the technical and strategic complexity of launching your new venture.' },
        { name: 'Content Creation', icon: Palette, href: '/services/content-creation', detail: 'High-fidelity narrative design', longDesc: 'Premium visual and written content that commands attention and communicates your brand story with precision.' },
      ]
    }
  };

  return (
    <nav
      className={`fixed top-0 left-0 w-full z-[200] transition-all duration-500 ${isScrolled ? 'py-4' : 'py-8'}`}
      onMouseLeave={() => {
        setShowMegaMenu(false);
        setHoveredItem(null);
      }}
    >
      <div className="max-w-7xl mx-auto px-6 relative">
        <div className={`relative z-[120] glass-morphism rounded-full px-8 py-4 flex items-center justify-between transition-all duration-500 ${isScrolled ? 'border-brand-medium/20 shadow-[0_10px_40px_rgba(0,0,0,0.5)]' : 'border-transparent'}`}>

          <Link href="/" className="flex items-center gap-3 group relative z-[210]" onClick={() => setIsOpen(false)}>
            <div className="w-10 h-10 rounded-full overflow-hidden border-2 border-brand-medium group-hover:scale-110 transition-transform duration-500 shadow-lg shadow-brand-medium/20">
              <img src="/logo.jpg" alt="Preet Tech Logo" className="w-full h-full object-cover" />
            </div>
            <span className={`text-xl font-black uppercase tracking-tighter transition-colors duration-500 ${isDark || isOpen ? 'text-white' : 'text-slate-900'}`}>
              PREET<span className="text-brand-sky">TECH.</span>
            </span>
          </Link>

          <div className="hidden lg:flex items-center gap-10">
            {links.map((link) => (
              <div
                key={link.name}
                className="relative"
                onMouseEnter={() => link.hasMegaMenu && setShowMegaMenu(true)}
              >
                <Link
                  href={link.href}
                  className={`text-[10px] font-black uppercase tracking-[0.3em] ${isDark ? 'text-slate-400 hover:text-white' : 'text-slate-500 hover:text-brand-deep'} transition-all py-2`}
                >
                  {link.name}
                </Link>
                {link.hasMegaMenu && showMegaMenu && (
                  <motion.div layoutId="nav-underline" className="absolute bottom-0 left-0 w-full h-0.5 bg-brand-medium" />
                )}
              </div>
            ))}
          </div>

          <div className="flex items-center gap-4 relative z-[210]">
            <button
              onClick={toggleTheme}
              className={`p-2 rounded-full transition-colors ${isOpen ? 'text-white/70 hover:text-white' : (isDark ? 'text-slate-400 hover:text-white' : 'text-slate-600 hover:text-brand-deep')}`}
              aria-label="Toggle Theme"
            >
              {isDark ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
            </button>

            <button className="hidden sm:flex items-center gap-2 px-6 py-2.5 bg-brand-deep dark:bg-white text-white dark:text-brand-deep rounded-full font-black text-[10px] uppercase tracking-widest hover:scale-105 transition-all shadow-xl">
              Get Quotes <ArrowUpRight className="w-3 h-3" />
            </button>

            <button
              className={`lg:hidden p-2 transition-colors relative z-[220] ${isOpen ? 'text-white' : (isDark ? 'text-white' : 'text-slate-900')}`}
              onClick={() => setIsOpen(!isOpen)}
            >
              {isOpen ? <X className="w-8 h-8 font-light" /> : <Menu className="w-7 h-7" />}
            </button>
          </div>
        </div>
      </div>

      {/* THE ARCHITECTURAL BENTO MEGA MENU */}
      <AnimatePresence>
        {showMegaMenu && (
          <motion.div
            initial={{ opacity: 0, y: 15 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: 15 }}
            transition={{ duration: 0.3, ease: "circOut" }}
            className="absolute top-full left-6 right-6 mt-4 hidden lg:block"
            onMouseEnter={() => setShowMegaMenu(true)}
          >
            <div className="bg-white dark:bg-[#050505] p-2 rounded-[2.5rem] border border-slate-200 dark:border-white/10 shadow-[0_30px_100px_rgba(0,0,0,0.2)] overflow-hidden">
              <div className="flex bg-slate-50/50 dark:bg-white/[0.02] rounded-[2rem] overflow-hidden min-h-[500px]">

                {/* Tab Switcher Panel */}
                <div className="w-1/4 p-8 border-r border-slate-100 dark:border-white/5 flex flex-col justify-between">
                  <div className="space-y-4">
                    <p className="text-[10px] font-black uppercase tracking-[0.4em] text-brand-medium mb-8">Capabilities</p>
                    {(Object.keys(megaMenuContent) as Array<keyof typeof megaMenuContent>).map((tab) => (
                      <button
                        key={tab}
                        onMouseEnter={() => {
                          setActiveTab(tab);
                          setHoveredItem(null);
                        }}
                        className={`w-full text-left p-4 rounded-2xl transition-all flex items-center justify-between group ${activeTab === tab ? 'bg-white dark:bg-white/10 shadow-sm text-brand-medium translate-x-2' : 'hover:translate-x-1 text-slate-500'}`}
                      >
                        <span className="font-bold uppercase text-sm tracking-tighter">{tab}</span>
                        <ChevronRight className={`w-4 h-4 transition-all ${activeTab === tab ? 'opacity-100' : 'opacity-0 -translate-x-2'}`} />
                      </button>
                    ))}
                  </div>

                  <div className="p-6 bg-brand-medium/5 dark:bg-brand-medium/10 rounded-3xl">
                    <p className="text-[9px] font-black uppercase tracking-widest text-brand-medium mb-3">Next Step</p>
                    <p className="text-xs font-bold text-foreground mb-4 leading-relaxed">Ready for a Strategic Audit?</p>
                    <button className="text-[10px] font-black uppercase text-brand-medium flex items-center gap-2 group hover:gap-3 transition-all">
                      Schedule <ArrowUpRight className="w-3 h-3" />
                    </button>
                  </div>
                </div>

                {/* Content Display Area */}
                <div className="flex-1 p-12 bg-white dark:bg-transparent relative">
                  <AnimatePresence mode="wait">
                    <motion.div
                      key={activeTab}
                      initial={{ opacity: 0, x: 20 }}
                      animate={{ opacity: 1, x: 0 }}
                      exit={{ opacity: 0, x: -20 }}
                      transition={{ duration: 0.3 }}
                      className="h-full flex flex-col"
                    >
                      <div className="mb-12 min-h-[100px] flex flex-col justify-end">
                        <AnimatePresence mode="wait">
                          {hoveredItem ? (
                            <motion.div
                              key={hoveredItem.name}
                              initial={{ opacity: 0, y: 10 }}
                              animate={{ opacity: 1, y: 0 }}
                              exit={{ opacity: 0, y: -10 }}
                              className="space-y-2"
                            >
                              <h3 className="text-4xl font-black text-brand-medium tracking-tighter uppercase">{hoveredItem.name}</h3>
                              <p className="text-slate-600 dark:text-slate-300 max-w-2xl leading-relaxed font-medium">{hoveredItem.longDesc}</p>
                            </motion.div>
                          ) : (
                            <motion.div
                              key={megaMenuContent[activeTab].title}
                              initial={{ opacity: 0, y: 10 }}
                              animate={{ opacity: 1, y: 0 }}
                              exit={{ opacity: 0, y: -10 }}
                            >
                              <h3 className="text-4xl font-black text-foreground tracking-tighter uppercase mb-4">{megaMenuContent[activeTab].title}</h3>
                              <p className="text-slate-500 dark:text-slate-400 max-w-lg leading-relaxed">{megaMenuContent[activeTab].desc}</p>
                            </motion.div>
                          )}
                        </AnimatePresence>
                      </div>

                      <div className="grid grid-cols-2 gap-x-8 gap-y-10 flex-1">
                        {megaMenuContent[activeTab].items.map((item, i) => (
                          <Link
                            key={i}
                            href={item.href}
                            className={`group flex gap-6 items-start transition-all origin-left ${hoveredItem?.name === item.name ? 'scale-[1.05]' : 'scale-100 opacity-70 hover:opacity-100'}`}
                            onMouseEnter={() => setHoveredItem(item)}
                            onClick={() => setShowMegaMenu(false)}
                          >
                            <div className={`w-12 h-12 rounded-xl border flex items-center justify-center flex-shrink-0 transition-all ${hoveredItem?.name === item.name ? 'bg-brand-medium text-white border-brand-medium shadow-lg shadow-brand-medium/20' : 'bg-slate-50 dark:bg-white/5 border-slate-100 dark:border-white/10 group-hover:bg-brand-medium/10'}`}>
                              <item.icon className={`w-6 h-6 transition-transform ${hoveredItem?.name === item.name ? 'scale-110' : 'text-brand-medium'}`} />
                            </div>
                            <div>
                              <h4 className={`font-bold uppercase tracking-tight mb-1 transition-colors ${hoveredItem?.name === item.name ? 'text-brand-medium' : 'text-foreground'}`}>{item.name}</h4>
                              <p className="text-xs text-slate-500 dark:text-slate-400 font-mono tracking-tight">{item.detail}</p>
                            </div>
                          </Link>
                        ))}
                      </div>

                      {/* Bottom Feature Pill */}
                      <div className="mt-auto pt-8 border-t border-slate-100 dark:border-white/5 flex items-center gap-12 text-slate-400">
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest">
                          <Shield className="w-3.5 h-3.5" />
                          Zero Upfront Friction
                        </div>
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest">
                          <Zap className="w-3.5 h-3.5" />
                          Performance Optimized
                        </div>
                        <div className="flex items-center gap-2 text-[10px] font-bold uppercase tracking-widest ml-auto text-brand-medium">
                          View Success Cases <ArrowUpRight className="w-3.5 h-3.5" />
                        </div>
                      </div>
                    </motion.div>
                  </AnimatePresence>
                </div>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Mobile Menu Portal */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            key="mobile-menu-portal"
            initial={{ clipPath: "circle(0% at calc(100% - 40px) 40px)", opacity: 0 }}
            animate={{ clipPath: "circle(150% at calc(100% - 40px) 40px)", opacity: 1 }}
            exit={{ clipPath: "circle(0% at calc(100% - 40px) 40px)", opacity: 0 }}
            transition={{ type: "spring", stiffness: 20, restDelta: 2 }}
            className="fixed inset-0 bg-[#070708] z-[105] flex flex-col lg:hidden overflow-hidden"
          >
            <div className="absolute inset-0 z-0 opacity-20">
              <div className="absolute top-0 right-0 w-[500px] h-[500px] bg-brand-medium/20 rounded-full blur-[120px] pointer-events-none" />
              <div className="absolute bottom-0 left-0 w-[400px] h-[400px] bg-brand-cyan/10 rounded-full blur-[100px] pointer-events-none" />
            </div>

            <div className="relative z-10 flex flex-col h-full pt-28 pb-12 px-8 overflow-y-auto">

              <AnimatePresence mode="wait">
                {!isMobileSubMenuOpen ? (
                  <motion.div
                    key="main-links-view"
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    exit={{ opacity: 0, x: 20 }}
                    transition={{ duration: 0.3 }}
                    className="flex flex-col h-full"
                  >
                    <div className="mb-8">
                      <p className="text-[10px] font-black uppercase tracking-[0.4em] text-brand-medium mb-2">Navigation</p>
                      <div className="h-px w-full bg-white/10" />
                    </div>

                    <div className="flex flex-col gap-4 mb-auto">
                      {links.map((link, i) => (
                        <motion.div
                          key={link.name}
                          initial={{ opacity: 0, x: -10 }}
                          animate={{ opacity: 1, x: 0 }}
                          transition={{ delay: i * 0.05 }}
                        >
                          {link.hasMegaMenu ? (
                            <button
                              onClick={() => setIsMobileSubMenuOpen(true)}
                              className="group flex flex-col gap-1 w-full text-left"
                            >
                              <div className="flex items-center justify-between">
                                <span className="text-3xl md:text-5xl font-black text-white group-hover:text-brand-medium transition-colors tracking-tighter uppercase leading-none">
                                  {link.name}
                                </span>
                                <ChevronRight className="w-5 h-5 text-white/10 group-hover:text-brand-medium group-hover:translate-x-1 transition-all" />
                              </div>
                              <span className="text-[10px] font-mono text-slate-500 uppercase tracking-widest">{link.desc}</span>
                            </button>
                          ) : (
                            <Link
                              href={link.href}
                              onClick={() => setIsOpen(false)}
                              className="group flex flex-col gap-1"
                            >
                              <div className="flex items-center justify-between">
                                <span className="text-3xl md:text-5xl font-black text-white group-hover:text-brand-medium transition-colors tracking-tighter uppercase leading-none">
                                  {link.name}
                                </span>
                                <ArrowUpRight className="w-5 h-5 text-white/10 group-hover:text-brand-medium group-hover:translate-x-1 group-hover:-translate-y-1 transition-all" />
                              </div>
                              <span className="text-[10px] font-mono text-slate-500 uppercase tracking-widest">{link.desc}</span>
                            </Link>
                          )}
                        </motion.div>
                      ))}
                    </div>

                    <div className="mt-12 space-y-8 pt-12 border-t border-white/10">
                      <div className="grid grid-cols-2 gap-8 text-white">
                        <div className="space-y-4">
                          <p className="text-[10px] font-black uppercase tracking-[0.3em] text-slate-500">Contact</p>
                          <a href="mailto:hello@preettech.com" className="text-xs font-bold hover:text-brand-medium transition-colors">hello@preettech.com</a>
                        </div>
                        <div className="space-y-4">
                          <p className="text-[10px] font-black uppercase tracking-[0.3em] text-slate-500">Operation</p>
                          <div className="text-xs font-bold whitespace-nowrap">Remote / New York</div>
                        </div>
                      </div>
                    </div>
                  </motion.div>
                ) : (
                  <motion.div
                    key="services-submenu-view"
                    initial={{ opacity: 0, x: 20 }}
                    animate={{ opacity: 1, x: 0 }}
                    exit={{ opacity: 0, x: -20 }}
                    transition={{ duration: 0.3 }}
                    className="flex flex-col h-full"
                  >
                    <button
                      onClick={() => setIsMobileSubMenuOpen(false)}
                      className="flex items-center gap-2 text-[10px] font-black uppercase tracking-[0.4em] text-brand-medium mb-8 hover:text-white transition-colors"
                    >
                      <ChevronLeft className="w-4 h-4" /> Go Back
                    </button>

                    <h3 className="text-3xl font-black text-white mb-10 tracking-tighter uppercase">Strategic Capabilities</h3>

                    <div className="flex flex-col gap-4 mb-auto overflow-y-auto pr-2">
                      {Object.values(megaMenuContent).flatMap(cat => cat.items).map((item, i) => (
                        <Link
                          key={i}
                          href={item.href}
                          onClick={() => setIsOpen(false)}
                          className="group flex gap-4 p-4 rounded-3xl bg-white/5 border border-white/5 hover:border-brand-medium/30 transition-all items-center"
                        >
                          <motion.div
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: i * 0.03 }}
                            className="flex items-center gap-4 w-full"
                          >
                            <div className="w-10 h-10 rounded-xl bg-brand-medium/10 flex items-center justify-center flex-shrink-0">
                              <item.icon className="w-5 h-5 text-brand-medium" />
                            </div>
                            <div className="flex-1">
                              <h5 className="font-bold text-white text-sm group-hover:text-brand-medium transition-colors uppercase tracking-tight">{item.name}</h5>
                              <p className="text-[10px] text-slate-500 leading-relaxed font-mono">{item.detail}</p>
                            </div>
                            <ChevronRight className="w-4 h-4 text-slate-700" />
                          </motion.div>
                        </Link>
                      ))}
                    </div>

                    <div className="mt-8 pt-8 border-t border-white/10">
                      <button className="w-full py-5 bg-white text-brand-deep rounded-full font-black text-sm uppercase tracking-widest shadow-xl">
                        Request Strategic Audit
                      </button>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>

            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </nav>
  );
};

export default Navbar;
